import navdata from '@/mock/navdata';
import {
    GET_NAV_TOP,
    REMOVE_NAV_TOP,
    INIT_NAV_TOP
} from '../action.type'
const defaultState = {
    navdata,
    selectedNavData:[]
}
const nav = (state = defaultState,action)=>{
    // console.log(action);
    switch(action.type){
        case INIT_NAV_TOP:
        //初始渲染导航
        {
            let {path} = action;
            let {navdata,selectedNavData} = state;
            path = path==='/home' ? '/home/index' : path;
            let target = navdata.find(item=>item.path===path);
            if(!target){
                navdata.map(item=>{
                    item.children && item.children.map(item=>{
                        if(item.path === path){
                            target = item;
                        }
                    })
                })
            }
            selectedNavData = [target];
            return {
                ...state,
                selectedNavData:[...selectedNavData]
            }
        }
        //添加顶部导航数据
        case GET_NAV_TOP:
        {
            let {path,name} = action;
            let {selectedNavData} = state;
            let index = selectedNavData.findIndex(item=>item.path===path);
            //1.之前有 拿出来 放到第一个
            if(index !== -1){
                let target = selectedNavData[index];
                selectedNavData.splice(index,1);
                selectedNavData.unshift(target);
            //2.之前没有直接追加到第一个
            }else{
                selectedNavData.unshift({
                    path,
                    name
                })
            }
            return {
                ...state,
                selectedNavData:[...selectedNavData]
            }
        }
        case REMOVE_NAV_TOP:
        {
            let {path} = action;
            let {selectedNavData} = state;
            let index = selectedNavData.findIndex(item=>item.path===path);
            selectedNavData.splice(index,1);
            return {
                ...state,
                selectedNavData:[...selectedNavData]
            }
        }
        default:
            return state;
    }
    
}

export default nav;