import request from '@/utils/request'
import Cookie from 'js-cookie'
import {
    GET_USER_INTO,
    EDIT_USER_INTO
} from '../action.type'
export const getUserInfo = function(that){
    return dispatch=>{
        request.get('/api/islogin').then(res=>{
            dispatch({
                type:GET_USER_INTO,
                info:res.info
            })
            that.setState({loginOpen:true});
        }).catch(res=>{
            if(res.status===401){
                that.props.history.push('/login');
            }
        })
    }
}

export const changeFace = (data)=>{
    return dispatch=>{
        request.post('/api/facePhoto',data).then(res=>{
            if(!res.code){
                Cookie.set('token',res.sessionId,{
                    expires:5
                })
                dispatch({
                    type:EDIT_USER_INTO,
                    info:res.info
                })
            }else{
                alert(res.messsage);
            }
        })
    }
}   

