import {
    Login,
    HomeContainer,
    HomeIndex,
    HomeOrder,
    HomeSettings
} from '@/views/'
import islogin from '@/components/islogin'

const routes = [
    {
        path:'/',
        redirect:'/login'
    },
    {
        path:'/login',
        component:Login
    },
    {
        path:'/home',
        component:islogin(HomeContainer),
        children:[
            {
                path:'/home/index',
                component:HomeIndex
            },
            {
                path:'/home/order/:ordername',
                component:HomeOrder
            },
            {
                path:'/home/settings',
                component:HomeSettings
            },
            {
                path:'/home',
                redirect:'/home/index'
            }
        ]
    }
]

export default routes;