import React from 'react'
import loadable from 'react-loadable'

function Loading (){
    return <div>loading...</div>
}
//实现组件异步加载
export const Login = loadable({
    loading:Loading,
    loader:()=>import('../views/login/index')
})

//实现组件异步加载
export const HomeContainer = loadable({
    loading:Loading,
    loader:()=>import('../views/home/index')
})

export const HomeIndex = loadable({
    loading:Loading,
    loader:()=>import('../views/home/components/index')
})

export const HomeOrder = loadable({
    loading:Loading,
    loader:()=>import('../views/home/components/order')
})

export const HomeSettings = loadable({
    loading:Loading,
    loader:()=>import('../views/home/components/setting')
})