import React from 'react';
import {
    Route,  //航线 path component/render  component
    Redirect,
    Switch
} from 'react-router-dom';

function RouterView({routes=[]}){
    const redirectArr = routes.filter(item=>item.redirect);
    return <Switch>
        {
            routes.map((item,key)=>!item.redirect && <Route key={key} path={item.path} render={(props)=>{
                return React.createElement(item.component,{
                    ...props,
                    routes:item.children
                })
            }} />).concat(
                redirectArr.map((item,key)=><Redirect key={key} to={item.redirect} from={item.path} />) 
            )
        }
    </Switch>
}


export default RouterView;