import {
    GET_LIST_DATA,
    FILTER_LIST_DATA
} from '../action.type'
import {handleState} from '@/mock/orderCol';
const defaultState = {
    listdata:[], //完整列表数据
    curdata:[] //当前所渲染的
}
const filterTitle = ['handleState','type','serviceName'];
const filterData = (data,item)=>{
    return Object.keys(data).every(title=>{
        if(filterTitle.includes(title)){ //状态 订单状态 客服名称
            return (data[title] === item[title] || data[title] === 'default')
        }else if(title === 'time'){ //日期
            if(data[title].length){
                let itemTime = new Date(item['date']);
                return (itemTime >= data[title][0] &&  itemTime <= data[title][1])
            }else{
                return true;
            }
        }else if(title === 'minMoney' || title === 'maxMoney'){
            let itemMoney = item.money
            return (itemMoney >= data['minMoney'] &&  itemMoney <= data['maxMoney'])
        }
    })
}
const list = (state=defaultState,action)=>{
    switch(action.type){
        case GET_LIST_DATA:
        {
            action.data = action.data.map(item=>{
                item = {...item}
                item.handleState = handleState(item.handleState)
                item.key = item.id;
                return item;
            });
            state.listdata = action.data;
            state.curdata = action.data;
            return {
                ...state,
                listdata:[...state.listdata],
                curdata:[...state.curdata]
            }
        }
           
        case FILTER_LIST_DATA:
        {
            let {listdata} = state;
            let {data} = action;
            state.curdata = listdata.filter(item=>{
                return filterData(data,item);
            })
            return {
                ...state,
                curdata:[...state.curdata]
            }
        }
        default:
            return state;
    }
    
}


export default list;